"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { ChevronLeft, ChevronRight, Plus } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog"
import { cn } from "@/lib/utils"
import { format, isSameDay, parseISO, startOfMonth, endOfMonth, eachDayOfInterval } from "date-fns"
import { Badge } from "@/components/ui/badge"

interface Task {
  id: string
  title: string
  taskDate: string
  status: 'pending' | 'completed' | 'canceled'
  jobName?: string
  technicianName?: string
  customerName?: string
  description?: string
}

interface TaskCalendarProps {
  tasks: Task[]
  onAddClick: () => void
  onEditClick: (task: Task) => void
  currentMonth: Date
  onMonthChange: (date: Date) => void
}

export function TaskCalendar({ tasks, onAddClick, onEditClick, currentMonth, onMonthChange }: TaskCalendarProps) {
  const [selectedDate, setSelectedDate] = useState<Date | null>(null)
  
  const year = currentMonth.getFullYear()
  const month = currentMonth.getMonth()
  
  const daysInMonth = eachDayOfInterval({
    start: startOfMonth(currentMonth),
    end: endOfMonth(currentMonth)
  })

  // Fill in blanks for start of week
  const startingDayOfWeek = daysInMonth[0].getDay()
  const blanks = Array(startingDayOfWeek).fill(null)

  const getTasksForDate = (date: Date) => {
    return tasks.filter(task => {
        const taskDate = new Date(task.taskDate)
        return isSameDay(taskDate, date)
    })
  }

  const prevMonth = () => {
    onMonthChange(new Date(year, month - 1, 1))
  }

  const nextMonth = () => {
    onMonthChange(new Date(year, month + 1, 1))
  }

  const monthNames = [
    "January", "February", "March", "April", "May", "June",
    "July", "August", "September", "October", "November", "December"
  ]

  const dayNames = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"]

  const getStatusColor = (status: string) => {
      switch(status) {
          case 'completed': return 'bg-green-100 text-green-800 border-green-200'
          case 'canceled': return 'bg-gray-100 text-gray-800 border-gray-200'
          default: return 'bg-yellow-100 text-yellow-800 border-yellow-200'
      }
  }

  return (
    <>
      <Card className="bg-white shadow-sm h-[calc(100vh-12rem)] min-h-[500px] flex flex-col">
        <CardHeader className="flex flex-row items-center justify-between pb-2 shrink-0">
          <CardTitle className="font-heading text-lg font-semibold text-[#0A2540]">
            {monthNames[month]} {year}
          </CardTitle>
          <div className="flex items-center gap-2">
            <Button variant="outline" size="icon" onClick={prevMonth}>
              <ChevronLeft className="h-4 w-4" />
            </Button>
            <Button variant="outline" size="icon" onClick={nextMonth}>
              <ChevronRight className="h-4 w-4" />
            </Button>
            <Button onClick={onAddClick} className="ml-2 bg-[#0A2540] hover:bg-[#0A2540]/90">
                <Plus className="h-4 w-4 mr-2" /> New Task
            </Button>
          </div>
        </CardHeader>
        <CardContent className="p-2 md:p-4 flex-1 flex flex-col">
          <div className="grid grid-cols-7 gap-1 flex-1 h-full">
            {dayNames.map((day) => (
              <div key={day} className="py-2 text-center text-sm font-medium text-muted-foreground bg-gray-50/50 rounded-t h-8">
                {day}
              </div>
            ))}
            
            {blanks.map((_, i) => (
                 <div key={`blank-${i}`} className="border-transparent" />
            ))}

            {daysInMonth.map((date, index) => {
              const dayTasks = getTasksForDate(date)
              const isToday = isSameDay(date, new Date())

              return (
                <div
                  key={index}
                  onClick={() => setSelectedDate(date)}
                  className={cn(
                    "min-h-[80px] rounded-lg border p-1 transition-all duration-200 ease-out overflow-hidden flex flex-col cursor-pointer hover:border-[#00C49A]/50 hover:bg-[#F6F9FC]",
                    isToday ? "border-[#00C49A] bg-[#00C49A]/5" : "border-border"
                  )}
                >
                  <div className={cn("text-sm font-medium mb-1 text-right px-1", isToday ? "text-[#00C49A]" : "text-[#333]")}>
                     {date.getDate()}
                  </div>
                  
                  <div className="space-y-1 overflow-y-auto flex-1 no-scrollbar">
                     {dayTasks.map(task => (
                         <div 
                            key={task.id} 
                            onClick={(e) => { e.stopPropagation(); onEditClick(task); }}
                            className={cn(
                                "text-[10px] px-1.5 py-0.5 rounded border truncate cursor-pointer hover:opacity-80",
                                getStatusColor(task.status)
                            )}
                            title={task.title}
                         >
                             {task.title}
                         </div>
                     ))}
                  </div>
                </div>
              )
            })}
          </div>
        </CardContent>
      </Card>

      <Dialog open={!!selectedDate} onOpenChange={() => setSelectedDate(null)}>
        <DialogContent className="max-w-md">
          <DialogHeader>
            <DialogTitle>Tasks for {selectedDate ? format(selectedDate, "PPP") : ""}</DialogTitle>
             <DialogDescription>
                 {selectedDate ? `${getTasksForDate(selectedDate).length} tasks scheduled` : ""}
             </DialogDescription>
          </DialogHeader>
          <div className="space-y-4 max-h-[60vh] overflow-y-auto">
             {selectedDate && getTasksForDate(selectedDate).map(task => (
                 <div 
                    key={task.id} 
                    className="p-3 border rounded-lg hover:bg-gray-50 cursor-pointer transition-colors"
                    onClick={() => { setSelectedDate(null); onEditClick(task); }}
                 >
                     <div className="flex justify-between items-start mb-1">
                         <span className="font-medium text-[#0A2540]">{task.title}</span>
                         <Badge variant="outline" className={cn("text-[10px] h-5", getStatusColor(task.status).replace("bg-", "text-").replace("text-", "border-"))}>
                             {task.status.toUpperCase()}
                         </Badge>
                     </div>
                     <div className="text-xs text-muted-foreground space-y-0.5">
                         <div>{format(new Date(task.taskDate), "p")} - {task.jobName}</div>
                         {task.technicianName && <div>Tech: {task.technicianName}</div>}
                     </div>
                 </div>
             ))}
             {selectedDate && getTasksForDate(selectedDate).length === 0 && (
                 <div className="text-center py-8 text-muted-foreground">
                     No tasks for this day.
                     <Button variant="link" onClick={() => { 
                         setSelectedDate(null); 
                         // Pre-fill date in form? We'd need to pass that up.
                         onAddClick(); 
                     }}>
                         Create one?
                     </Button>
                 </div>
             )}
          </div>
        </DialogContent>
      </Dialog>
    </>
  )
}
